body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(left, rgb(143, 201, 220), rgb(33, 92, 97));
  background: linear-gradient(to right, rgb(143, 201, 220), rgb(33, 92, 97));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


img {
	width: 200px;
	height: 200px;
}
@font-face {
font-family: 'SEGA LOGO FONT';
font-style: normal;
font-weight: normal;
src: local('SEGA LOGO FONT'), url(/robotsearch/static/media/SEGA.12a3db8b.woff) format('woff');
}

h1 {
	font-family: 'SEGA LOGO FONT';
	color: rgb(107, 199, 224);
	font-weight: 200;
}

